.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  border-radius: 12px;
  background-color: rgb(60, 62, 68);
  transform: scale(0.5);
  transition: 0.4s all;
}

.modal__content.active{
  transform: scale(1);
}
.area {
  background-color: rgb(36, 40, 47);
  display: flex;
  justify-content: center;
}

